import { Component, OnInit, Output, EventEmitter } from '@angular/core';

import { Approval } from '../../classes/approval';
import { ApprovalService } from '../../angular-services/approval.service';

@Component({
  selector: 'app-approvals',
  templateUrl: './approvals.component.html',
  styleUrls: ['./approvals.component.css']
})
export class ApprovalsComponent implements OnInit {

  /* counters = [
    { title: 'ENEM', quantity: '52' },
    { title: 'Concursos', quantity: '43' },
    { title: 'IFRN', quantity: '23' }
  ]; */
  @Output() loading = new EventEmitter<boolean>();
  approvals: Approval[] = [];

  constructor(private approvalService: ApprovalService) { }

  ngOnInit() {
    this.getApprovals();
  }

  getApprovals() {
    this.loading.emit(true);
    this.approvalService.getApprovals().subscribe(
      approvals => {
        this.approvals = approvals;
      }
    );
  }

}
